.footer {
  background-color: $color-black-russian;
  color: $color-default-white;
}

.footer__wrapper {
  width: 1160px;

  display: grid;
  grid-template-columns: auto auto auto;
  justify-content: space-between;

  margin: 0 auto;

  @include vp-1365 {
    width: 678px;
  }

  @include vp-767 {
    width: 290px;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto auto;
  }
}

.footer__logo {
  color: $color-default-white;
  font-size: 26px;
  line-height: 31px;
  font-family: $ff-alumni;
  font-weight: 600;
  letter-spacing: 0.52px;

  padding: 44px 30px 32px 0;

  @include vp-1365 {
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
    letter-spacing: 0.48px;

    padding: 47px 62px 49px 0;
  }

  @include vp-767 {
    padding: 50px 92px 0 0;

    grid-row: 1 / 2;
    grid-column: 1 / -1;
  }
}

.footer__links-list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  display: flex;
  justify-content: center;
  align-items: center;

  @include vp-767 {
    display: grid;
    gap: 30px;
    grid-row: 1 / 3;
    grid-column: 1 / -1;
    grid-template: repeat(3, 1fr) / repeat(2, 1fr);
    grid-auto-flow: column;
    margin-top: 53px;
  }
}

.footer__links-list-item:not(:last-child) {
  margin-right: 40px;

  @include vp-1365 {
    margin-right: 30px;
  }

  @include vp-767 {
    margin: 0;
  }
}

.footer__link {
  color: $color-default-white;
  font-size: 14px;
  line-height: 16px;
  font-family: $ff-roboto;
}

.footer__socials-list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  display: flex;
  justify-content: center;
  align-items: center;

  @include vp-767 {
    grid-column: 1 / -1;
    justify-self: flex-start;
    margin: 40px 0 52px 0;
  }
}

.footer__socials-list-item {
  min-width: 25px;
  min-height: 25px;
}

.footer__socials-list-item:not(:last-child) {
  margin-right: 11px;
}

.footer__links-list-item:first-child {
  grid-row-start: 2;
}

.footer__socials-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-radius: 50%;
  background-color: $color-default-white;

  @include hover-focus {
    color: $color-default-white;
    background-color: $color-egyptian-blue;
  }

  @include active {
    color: $color-default-white;
    background-color: $color-default-black;
  }

  &:disabled {
    opacity: 0.3;
  }
}
